.auth__button{
max-width: 358px;
width: 100%;
height: 50px;
background: #FFFFFF;
border-radius: 2px;
border: none;

margin: 0 0 15px;

font-family: 'Inter' Arial, Helvetica, sans-serif;
font-style: normal;
font-weight: 400;
font-size: 18px;
line-height: 22px;

color: #000000;

cursor: pointer;
opacity: 1;
transition: opacity 0.4s ease;

}

.auth__button:hover {
   opacity: 0.85;
 }