.header {
  max-width: 880px;
  width: 100%;
  margin: 0 auto;
  padding: 46px 0 39px;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  box-sizing: border-box;

  display: flex;
  justify-content: space-between;
}
@media screen and (max-width: 1023px) {
  .header {
    padding: 28px 0 26.6px 27px;
  }
}
